<template>
  <div class="home">
    <!--轮播图-->
    <van-swipe :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="img in swipes">
        <van-image
                width="100vw"
                height="30vw"
                :src="img"
        >
          <template v-slot:error>加载失败</template>
          <template v-slot:loading>
            <van-loading type="spinner" size="20" />
          </template>
        </van-image>
      </van-swipe-item>
    </van-swipe>
    <!--公告-->
    <notice :info="notices"></notice>
    <!--充值兑现-->
    <money class="money"></money>
    <!--帮助意见-->
    <div class="feedback-box">
      <div class="help-center" v-on:click="toHelp">
        <van-image
                width="90%"
                height="90%"
                :src="help_src"
        >
          <template v-slot:error>加载失败</template>
          <template v-slot:loading>
            <van-loading type="spinner" size="20" />
          </template>
        </van-image>
      </div>
      <div class="ideas-box">
        <div class="border-bottom" v-on:click="toTeam">
          <p class="title-p">我的团队</p>
          <p class="desc-p">组队赚更多红利</p>
        </div>
        <div v-on:click="toIdea">
          <p class="title-p">意见反馈</p>
          <p class="desc-p">提交您的建议，我们会做的更好</p>
        </div>
      </div>
    </div>
    <!--趋势列表-->
    <div class="trend-list-box">
      <div class="trend-list-title trend_list">
        <div class="title title1">COIN</div>
        <div class="title">最新价</div>
        <div class="title">涨跌幅</div>
      </div>
      <van-list
              v-model="loading"
              :finished="finished"
              finished-text=""
              @load="onLoad"
              :error.sync="error"
              error-text="请求失败，点击重新加载"
      >
        <div class="trend_list"
                v-for="item in list"
             :key="item.id"
             :title="item">
          <div class="trend-list-item1">
            <div class="coin-image">
              <van-image
                      :width="icon_w"
                      :height="icon_h"
                      :src="item.icon"
              >
                <template v-slot:error>加载失败</template>
                <template v-slot:loading>
                  <van-loading type="spinner" size="20" />
                </template>
              </van-image>
            </div>
            <div class="coin-text">
              <p class="coin-text-name">{{item.name}}</p>
              <p class="coin-text-desc font2">{{item.desc}}</p>
            </div>
          </div>
          <div class="trend-list-item2">{{priceFormat(item.price)}}</div>
          <div class="trend-list-item3">
            <van-tag v-bind:type  ="check(item.percent)" size="medium" style="width: 12vw;height: 5vw;line-height: 5vw;">{{item.percent}}</van-tag>
          </div>
        </div>
      </van-list>
    </div>
    <!--弹出层-->
    <van-popup
            v-model="poup_show"
            position="right"
            get-container="#app"
            :style="{width:'100vw', height: '100vh', background:'white',}"
            :duration="0.3"
            :overlay="false"
    >
      <about-us v-if="poup_page==1"></about-us>
      <idea v-else-if="poup_page==2"></idea>
      <help v-else></help>
    </van-popup>
  </div>
</template>
<script>
  import { Swipe, SwipeItem, Image, Loading, List, Tag, Popup } from 'vant';
  import notice from "@/components/common/notice.vue";
  import money from "@/components/common/money.vue";
  import help from "@/components/common/help.vue";
  import idea from "@/components/common/idea.vue";
  import aboutUs from "@/components/common/aboutUs.vue";
export default {
  components: {
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Image.name]: Image,
    [Loading.name]: Loading,
    [List.name]: List,
    [Tag.name]: Tag,
    [Popup.name]: Popup,
    notice,
    money,
    help,
    idea,
    aboutUs
  },
  name: 'Home',
  data(){
    return {
      poup_show: false,
      poup_page: 'help',
      icon_h: '75%',
      icon_w: '75%',
      help_src: 'https://img.yzcdn.cn/vant/cat.jpeg',
      list: [],
      loading: false,
      finished: false,
      error: false,
      swipes: [
        'https://img.yzcdn.cn/vant/cat.jpeg',
        'https://img.yzcdn.cn/vant/cat.jpeg',
        'https://img.yzcdn.cn/vant/cat.jpeg',
        'https://img.yzcdn.cn/vant/cat.jpeg',
      ],
      notices: [
        {
          id: 1,
          title: '很快它就会失控。这也是我们针对需要向任意更深层级的组件提供上下文信息时推荐依赖注入的原因。',
          content: '1111111111111111111'
        },{
          id: 2,
          title: '该页面假设你已经阅读过了组件基础。如果你还对组件不太了解，推荐你先阅读它。',
          content: '22222222222222',
        },{
          id: 3,
          title: '在绝大多数情况下，我们最好不要触达另一个组件实例内部或手动操作 DOM 元素。不过也确实在一些情况下做这些事情是合适的。',
          content: '333333333333333333333333'
        }
      ]
    }
  },
  created(){
    this.$parent.active = 0;
  },
  methods:{
    check (percent) {
      if(Number(percent.replace('%', '')) < 0){
        return 'danger';
      }
      return 'success';
    },
    priceFormat (price) {
      return price;
    },
    goBack () {
      window.history.length > 1
              ? this.$router.go(-1)
              : this.$router.push('/')
    },
    toHelp () {
      // this.poup_page = 0;
      // this.poup_show = true;
        this.$router.push({path:'/help'})
    },
    toTeam () {
      // this.poup_page = 1;
      // this.poup_show = true;
        this.$router.push({path:'/aboutus'})
    },
    toIdea () {
      // this.poup_page = 2;
      // this.poup_show = true;
        this.$router.push({path:'/idea'})
    },
    onLoad () {
      this.list = [
        {
          id: 0,
          icon: 'https://cn.vuejs.org/images/logo.png',
          name: 'SHT',
          desc: 'SHT',
          price: 0.33,
          percent: '-1.37%',
        },{
          id: 1,
          icon: 'https://cn.vuejs.org/images/logo.png',
          name: 'XRP',
          desc: 'XRP',
          price: 2.92,
          percent: '-2.86%',
        },{
          id: 2,
          icon: 'https://cn.vuejs.org/images/logo.png',
          name: 'LTC',
          desc: 'LTC',
          price: 947.88,
          percent: '1.27%',
        },{
          id: 3,
          icon: 'https://cn.vuejs.org/images/logo.png',
          name: 'ETH',
          desc: 'ETH',
          price: 1874.00,
          percent: '0.16%',
        },{
          id: 4,
          icon: 'https://cn.vuejs.org/images/logo.png',
          name: 'ETC',
          desc: 'ETC',
          price: 58.11,
          percent: '0.01%',
        },{
          id: 5,
          icon: 'https://cn.vuejs.org/images/logo.png',
          name: 'EOS',
          desc: 'EOS',
          price: 47.02,
          percent: '-1.92%',
        },{
          id: 6,
          icon: 'https://cn.vuejs.org/images/logo.png',
          name: 'DASH',
          desc: 'DASH',
          price: 902.00,
          percent: '0%',
        },{
          id: 7,
          icon: 'https://cn.vuejs.org/images/logo.png',
          name: 'BTC',
          desc: 'BTC',
          price: 64600000.00,
          percent: '0.39%',
        }
      ];
      this.finished = true;
      this.loading = false;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scope  d>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

.home{
  width: 100vw;
  height: 100vh;
  background: #f5f5f5;
  overflow: auto;
}
.money{
  margin-top: 3px;
}
  .feedback-box{
    display: flex;
    background: white;
    width: 100%;
    height: 25vw;
    margin-top: 5px;
    padding: 0px;
    border-spacing: 0px ;
  }
  .help-center{
    width: 45%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .ideas-box{
    width: 55%;
    height: 100%;
    text-align: left;
  }
  .ideas-box>div{
    height: 50%;
    width: 95%;
    box-sizing: border-box;
    padding: 2vw 0px;
  }

  .border-bottom{
    border-bottom: 1px solid #F6F6F6;
  }
  .title-p{
    font-size: 4vw;
    width: 100%;
  }
  .desc-p{
    width: 100%;
    font-size: 3vw;
  }
  .trend-list-box{
    width: 100%;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0px 2vw;
    background: white;
    margin-top: 5px;
    margin-bottom: 15vw;
  }
  .trend_list{
    display: flex;
    justify-content: space-between;
    height: 13vw;

  }
  .trend_list>.title1,.trend_list>.trend-list-item1{
    width: 60vw;
    text-align: left;
    display: flex;

  }
.trend_list>.trend-list-item1>.coin-image{
  width: 10vw;
  height: 10vw;
  font-size: 3vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.trend_list>.trend-list-item1>.coin-text{
  width: 50vw;
}
.trend_list>.trend-list-item1>.coin-text>p{
  line-height: 5vw;
}
.trend_list>.trend-list-item1>.coin-text>.coin-text-name{

}
.trend_list>.title{
  font-size: 3vw;
  height: 8vw;
  box-sizing: border-box;
  padding-top: 2.5vw;
}


</style>
