<template>
    <div class="notice-section">
      <span class="icon">
        <van-icon name="volume-o" />
      </span>
        <div class="notice-box" v-if="info.length">
            <div class="notice-show notice-slide" v-on:click="doSomething(title[0], this)">{{ title[0] ? title[0].title : '' }}</div>
            <div class="notice-hide notice-slide" v-on:click="doSomething(title[1], this)">{{ title[1] ? title[1].title : '' }}</div>
        </div>
        <div class="notice-box" v-else>暂无公告</div>

        <!--弹出层-->
        <van-popup
                v-model="poup_show"
                position="right"
                get-container="#app"
                :style="{width:'100vw', height: '100vh', background:'white',}"
                :duration="0.3"
                :overlay="false"
        >
            <notice-content :content="notice_content"></notice-content>
        </van-popup>
    </div>
</template>

<script>
    import { Icon, Popup } from 'vant';
    import noticeContent from "@/components/common/noticeContent.vue";

    export default {
        name: "notice",
        props: {
            info: [Array, Object],
            slide_time: {
                type: Number,
                default: 0.8,
            },
            auto_time: {
                type: Number,
                default: 1300,
            },
        },
        components: {
            [Icon.name]: Icon,
            [Popup.name]: Popup,
            noticeContent
        },
        data() {
            return {
                poup_show: false,
                notice_content: {},
                handle: null,
                elements: [],
                title: [],
                title_index: 0,
                index: 0,
                change_flag: true,
                // auto_time: 3000,
                // slide_time: 0.8,
                example_data: [
                ],

                isOver: 0,
            }
        },
        mounted() {
            this.title.push(this.info[this.index]);
            this.index++;
            if(this.index >= this.info.length){
                this.index = 0;
            }
            this.title.push(this.info[this.index]);
            this.index++;
            this.beginTransition();
        },
        beforeDestroy() {
            if(this.handle){
                clearInterval(this.handle);
            }
        },
        methods: {
            doSomething(item, that){
                console.log(item, that)
                this.notice_content = item;
                this.poup_show = true;
                // this.$router.push({path:'/notice_content', query:{ content: item}})
            },
            initEvent(){
                document.querySelector('.notice-box').style='position:relative;'
                this.elements = document.querySelectorAll('.notice-box div');
                let _this = this;
                let style = 'transition: all '+_this.slide_time+'s; -o-transition:all ';
                style +=_this.slide_time+'s;-ms-transition:all ';
                style +=_this.slide_time+'s;-moz-transition:all ';
                style +=_this.slide_time+'s;-webkit-transition:all ';
                style +=_this.slide_time+'s;position:absolute;';
                this.elements.forEach(function (ele, index){
                    ele.style = style;
                    ele.addEventListener('transitionstart', function (e) {
                        // ele.classList.add('notice');
                    });
                    ele.addEventListener('transitionend', function (e) {
                        _this.isOver++;
                        if(_this.isOver == 2){
                            if(ele.classList.value.indexOf('notice-hide-top') >= 0){
                                ele.classList.add('notice-hide');
                                ele.classList.remove('notice-hide-top');
                                if(_this.index >= _this.info.length){
                                    _this.index = 0;
                                }
                                _this.title[_this.title_index] = _this.info[_this.index];
                                _this.$forceUpdate();
                                if(_this.title_index == 1){
                                    _this.title_index = 0;
                                }else{
                                    _this.title_index = 1;
                                }
                                _this.index++;
                            }
                        _this.isOver = 0;
                        }
                    })
                })
            },
            beginTransition(){
                this.initEvent();
                let _this = this;
                this.handle = setInterval(function () {
                    _this.elements.forEach(function (ele, index) {
                        if(ele.classList.value.indexOf('notice-show') >= 0){
                            ele.classList.remove('notice-show');
                            ele.classList.add('notice-hide-top');
                        }else{
                            ele.classList.remove('notice-hide');
                            ele.classList.add('notice-show');
                        }
                    })
                }, this.auto_time+this.slide_time*1000)
            }
        }
    }
</script>

<style scoped>
    .notice-section{
        display: flex;
    }
    .icon{
        display: inline-block;
        width: 10vw;
        height: 10vw;
        line-height: 11vw;
        background: white;
    }
    .notice-box{
        width: 90vw;
        height: 10vw;
        line-height: 10vw;
        background: white;
        position: relative;
        overflow: hidden;
        text-align: left;
    }

    .notice-show{
        -webkit-transform: translate(0, 0);
        -moz-transform: translate(0, 0);
        -ms-transform: translate(0, 0);
        -o-transform: translate(0, 0);
        transform: translate(0, 0);
        opacity: 1;
        z-index: 10;
    }

    .notice-hide{
        -webkit-transform: translate(0, 100%);
        -moz-transform: translate(0, 100%);
        -ms-transform: translate(0, 100%);
        -o-transform: translate(0, 100%);
        transform: translate(0, 100%);
        opacity: 0;
    }

    .notice-hide-top{
        -webkit-transform: translate(0, -100%);
        -moz-transform: translate(0, -100%);
        -ms-transform: translate(0, -100%);
        -o-transform: translate(0, -100%);
        transform: translate(0, -100%);
        opacity: 0;
        z-index: -10;
    }

    .notice-slide{
        white-space: nowrap;
        text-overflow: ellipsis;
        width: 100%;
        overflow: hidden;
        text-overflow-ellipsis: true;
    }
</style>